import "./styles/index.scss";

import Display from './display'


const canvas = document.getElementById('audio-note');
export const display = new Display(canvas);


const synth = new Tone.Synth({
    "oscillator": {
        "type": "sine"
    },
    "envelope": {
        "attack": 0.1,
        "decay": 0.15,
        "sustain": 0.3,
        "release": 0.4
    }
})

const gain = new Tone.Gain(0.5)
gain.toMaster()

synth.connect(gain)

const c4 = document.querySelectorAll('.checkbox-row25');

for (let i = 0; i < c4.length; i++) {
    c4[i].addEventListener('click', async () => {
        await ToneNote.start();
        synthNote.triggerAttackRelease('C4', '8n').volume.value = 3;
    });
}